:root {
	--color-white: #fff;
	--color-black: #000;
	--color-yellow: #ffff00;
	--color-dark-gray: #10100e;
	--color-blue: #6a83fb;
	--color-violet: #ae72c2;
	--color-red: #f75d83;

	--spacing-small: 10px;
	--spacing-ultrasmall: 10px;
	--spacing-medium: 20px;
	--spacing-large: 30px;

	--margin-list: 7px;
	--width-max: 1200px;

	--font-family-main: sans-serif;
	--font-size-header: 26px;
	--font-size-subheader: 18px;
	--font-size-main: 16px;
	--font-bold: 600;
	--text-shadow-yellow: 0px 0px 2px var(--color-white), 0px 0px 40px var(--color-yellow);
	--text-shadow-blue: 0px 0px 2px var(--color-white), 0px 0px 40px var(--color-blue);

	--box-shadow-white: 0 16px 24px 2px rgba(255, 255, 255, 0.02),
		0 6px 30px 5px rgba(255, 255, 255, 0.04), 0 8px 10px -5px rgba(255, 255, 255, 0.1);

	--box-shadow-black: 0 16px 24px 2px rgba(0, 0, 0, 0.5), 0 6px 30px 5px rgba(0, 0, 0, 0.1),
		0 8px 10px -5px rgba(0, 0, 0, 0.2);

	--border-radius-small: 3px;
	--border-radius-medium: 5px;

	--theme-light-header: #dfe2df;
	--theme-dark-header: #ca6c6f;
	--theme-neutral-header: #cccc46;
	--theme-default-header: var(--theme-neutral-header);

	--theme-light-bg-image: linear-gradient(
		to right bottom,
		#051937,
		#162454,
		#312d70,
		#533389,
		#7a359f
	);
	--theme-dark-bg-image: linear-gradient(
		to right bottom,
		#000000,
		#160b11,
		#20141e,
		#281b2e,
		#2b2440
	);
	--theme-neutral-bg-image: url(@static/bg.jpg);
	--theme-default-bg-image: var(--theme-neutral-bg-image);

	--transition: 0.4s;
}
*,
*:active,
*:before {
	box-sizing: border-box;
}

body {
	min-height: 100vh;
	margin: 0;
	padding: 0;
	overflow-y: scroll;
	background-color: var(--color-dark-gray);
	font-family: var(--font-family-main);
	font-size: var(--font-size-main);
	background-image: var(--theme-default-bg-image);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top left;
	background-attachment: fixed;
}

img {
	display: block;
}
.header__text {
	line-height: 1.7;
	font-size: var(--font-size-header);
	text-shadow: 0 0 2px var(--color-black);
	color: var(--theme-default-header);
	transition: color var(--transition);
}
/***************************************************
SCROLL
/***************************************************/
/* Firefox */
* {
	scrollbar-color: rgb(37, 56, 97) rgba(0, 0, 0, 0.7);
	scrollbar-width: thin;
}

/* Chrome */
::-webkit-scrollbar {
	width: 8px;
	height: 8px;
}
::-webkit-scrollbar-thumb {
	background-color: rgb(37, 56, 97);
	border-radius: 2px;
	transition: var(--transition);
}
::-webkit-scrollbar-track {
	background-color: rgba(0, 0, 0, 0.7);
}
::-webkit-scrollbar-corner {
	background-color: rgba(0, 0, 0, 0.7);
}
